function getTeam () {
  fetch(
    'https://cdn.contentful.com/spaces/bv76lgjagl2f/environments/master/entries?access_token=XAJ1wOYnMT2Aow6tufgnBbNDaU98fL0aJoB8ygy2ebA&content_type=teammate&include=1&order=fields.name'
  )
    .then(response => response.json())
    .then(data => {
      const section = document.querySelector('.team')

      data.items.map((item, index) => {
        const imgId = item.fields.headshot.sys.id

        fetch(
          `https://cdn.contentful.com/spaces/bv76lgjagl2f/environments/master/assets/${imgId}?access_token=XAJ1wOYnMT2Aow6tufgnBbNDaU98fL0aJoB8ygy2ebA`
        )
          .then(response => response.json())
          .then(data => {
            const img = data.fields.file

            const el = document.createElement('a')
            el.setAttribute('data-name', item.fields.name)
            if (item.fields.link) {
              el.href = item.fields.link
              el.target = '_blank'
            }
            el.innerHTML = `
              <figure class="team__member">
                <img src="${img.url}" alt="${item.fields.name}">
                <figcaption>
                  <p>
                  ${item.fields.name}
                  </p>
                  ${
                    item.fields.link ? '<div class="team__linkedin"></div>' : ''
                  }
                  
                  <p>${item.fields.position}</p>
                </figcaption>
              </figure>
        `
            section.append(el)
          })
      })
    })
}

getTeam()
